export const positions = [
    'static',
    'relative',
    'fixed',
    'absolute',
    'sticky'
]
export const absolutePosition = [
    'absolute--center',
    'absolute-x--center',
    'absolute-y--center'
]
export const positionPlacement = [
    'top',
    'right',
    'bottom',
    'left',
    'inset',
    'inset-x',
    'inset-y'
]
export const positionValues = [
    '0'
]

export const positionsCodeString = positions.map( pos => `<div class="${ pos }">...</div>\n` )
export const absolutePositionCodeString = absolutePosition.map( pos => `<div class="relative">\n   <div class="${ pos }">...</div>\n</div>\n` )
export const positionValuesCodeString = positionPlacement.map( placement =>
    positionValues.map( val =>
        `<div class="relative">\n   <div class="absolute ${ placement }--${ val }">...</div>\n</div>\n`
    ).join('')
)
