import * as React from "react"
import SEO from "../../components/seo";
import { Child, CodeSample, ExampleContainer, SectionWrapper } from "../../components/ExampleComponents";
import { absolutePosition, absolutePositionCodeString, positionPlacement, positions, positionsCodeString, positionValues, positionValuesCodeString } from "../../data/position";

const Position = () => {
    return (
        <>
            <SEO title={ "Position" }/>
            <h1>Position</h1>
            <p>The class constructor looks like this:</p>
            { positions.map( pos => <CodeSample key={pos} label={ `For ${ pos }` } codeString={ pos }/> ) }

            <SectionWrapper id={ 'position-absolute' } codeString={ absolutePositionCodeString.join( '' ) }>
                { absolutePosition.map( pos => {
                    return (
                        <ExampleContainer key={pos} className={ "relative" } customLabel={ pos }>
                            <Child className={ `${ pos }` }>{ pos }</Child>
                        </ExampleContainer>
                    )
                } ) }
            </SectionWrapper>

            <SectionWrapper id={ 'position-values' } codeString={ positionValuesCodeString.join( '' ) }>
                <p>Position values can be used in <span className={ "text--primary fira" }>.absolute</span>, <span className={ "text--primary fira" }>.fixed</span> and <span className={ "text--primary fira" }>.sticky</span> positions. The default values are <span className={ "text--primary fira" }>"0"</span> and <span className={ "text--primary fira" }>"auto"</span> but you can add as many as you need.</p>
                <div className={ "flex--wrap" }>
                    { positionPlacement.map( placement => (
                        <ExampleContainer key={placement} wrapperClassName={ "col--6 lg__col--3 px--md" } className={ "relative p--md" } customLabel={ placement + "--" + 0 }>
                            <Child className={ `absolute flex--center-middle ${ placement }--0` }/>
                        </ExampleContainer>
                    ) ) }
                </div>
            </SectionWrapper>
        </>
    )
}

export default Position
