import { CSSProperties, FunctionComponent, memo } from "react";
import * as React from "react";
import SyntaxHighlight from "./SynaxHighlight";
import { createLabel } from "../utilities";

interface sectionWrapperProps {
    id: string
    codeString?: string
    headingSize?: string
}

export const SectionWrapper: FunctionComponent<sectionWrapperProps> = ( { id, children, codeString, headingSize } ) => {
    return (
        <section id={ id } className={ "pt--xs" }>
            <h2 className={ headingSize }>{ createLabel( id ) }</h2>
            { children }
            { codeString && <SyntaxHighlight lang={ 'html' } codeString={ codeString }/> }
        </section>
    )
}

interface ExampleContainerProps {
    className?: string,
    wrapperClassName?: string,
    customLabel?: string,
    isDefault?: boolean,
    grid?: boolean,
    useHeight?: boolean,
    wrap?: boolean,
    height?: number
}

export const ExampleContainer: FunctionComponent<ExampleContainerProps> = memo( ( { children, className, wrapperClassName, isDefault, grid, useHeight = true, height = 120, wrap, customLabel } ) => {
    return (
        <div className={ wrapperClassName }>
            { className ? <small className={ "block mb--xs" }><code>.{ customLabel ? customLabel : className } { isDefault && '(default)' }</code></small> : "" }
            <div style={ {
                minHeight          : useHeight && 120,
                height             : useHeight && height,
                gridTemplateColumns: " repeat(auto-fit, minmax(300px, 1fr) )"
            } }
                 className={ `${ grid ? "grid" : "flex" } ${ wrap ? "flex--wrap" : "" } bg--bgDimmed text--text brd--border brd--md brd--border p--xs mb--md round ${ className }` }>
                { children }
            </div>
        </div>
    )
} )

export const Child: FunctionComponent<{ className?: string, fullwidth?: boolean, style?: CSSProperties }> = memo( ( { className, fullwidth, children, style } ) => {
    return (
        <div style={ style } className={ `${ fullwidth ? "w--100" : "" } p--md bg--bg text--text brd--md brd--bgDimmed round ${ className ? className : "" }` }>{ children ? children : "Child" }</div>
    )
} )

export const CodeSample: FunctionComponent<{ label: string, codeString: string }> = ( { label, codeString } ) => {
    return (
        <>
            <small className={ "block p--xs" }>{ label }</small>
            <SyntaxHighlight lang={ 'html' } codeString={ codeString }/>
        </>
    )
}
